main.product-page nav {
  background: rgb(206, 71, 21);
}
main.product-page div.background-image.search-background div.search-background {
  backdrop-filter: blur(3px);
  position: relative;
}
main.product-page main {
  min-height: calc(100vh - 64px);
}
main.product-page main section.product-suggestion,
main.product-page main section.cont-ingredients,
main.product-page main section.cont-info {
  width: 90%;
  margin: 0 auto;
}
main.product-page main section.basic-info {
  display: flex;
  justify-content: space-around;
  padding: 4em 0;
}
main.product-page main section.basic-info section.image {
  height: 80vh;
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
main.product-page main section.basic-info section.image figure img {
  width: 100%;
}
main.product-page main section.basic-info section.details {
  width: 40%;
}
main.product-page main section.basic-info section.details h1.name,
main.product-page main section.basic-info section.details h3.category,
main.product-page main section.basic-info section.details h2.price {
  font-family: "KoHo", sans-serif;
  font-weight: 600;
}
main.product-page main section.basic-info section.details h1.name {
  font-size: clamp(2.2rem, 8vw, 3.5rem);
  font-weight: 700;
}
main.product-page main section.basic-info section.details h3.category {
  font-size: clamp(1rem, 4vw, 1.5rem);
}
main.product-page main section.basic-info section.details p {
  font-weight: 500;
  font-family: "KoHo", sans-serif;
  margin-top: 1.5em;
}
main.product-page main section.basic-info section.details h2.price {
  font-size: clamp(1.75rem, 4vw, 2.25rem);
  margin: 2rem 0;
}
main.product-page main section.basic-info section.details button {
  width: 100%;
  padding: 1em 0;
  border-radius: 2em;
  outline: 0;
  border: 0;
  background: rgb(59, 96, 208);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f0f0f0;
  text-transform: uppercase;
  cursor: pointer;
}
main.product-page main section.basic-info section.details button, main.product-page main section.basic-info section.details button > * {
  transition: 0.3s;
}
main.product-page main section.basic-info section.details button span {
  height: 20px;
  aspect-ratio: 1;
  display: block;
  margin-left: 0.5em;
}
main.product-page main section.basic-info section.details button span svg {
  width: 100%;
  height: 100%;
}
main.product-page main section.basic-info section.details button label {
  display: block;
  font-weight: 600;
  font-family: "KoHo", sans-serif;
  font-size: 1.2rem;
}
main.product-page main section.basic-info section.details button:hover {
  background: rgb(44, 74, 164);
}
main.product-page main section.basic-info section.details button:hover span {
  margin-left: 1.5em;
}
main.product-page main section.basic-info section.details button:hover span,
main.product-page main section.basic-info section.details button:hover label {
  transform: scale(1.2);
}
main.product-page main section.basic-info section.details div.size-select {
  margin: 2.5rem 0;
}
main.product-page main section.basic-info section.details div.size-select h4 {
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1em;
}
main.product-page main section.basic-info section.details div.size-select ul {
  display: flex;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
main.product-page main section.basic-info section.details div.size-select ul li {
  width: 20%;
  min-width: 60px;
  aspect-ratio: 2;
  border-radius: 0.15em;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
  color: #151515;
  cursor: pointer;
  transition: 0.3s;
  font-weight: 700;
  font-family: "KoHo", sans-serif;
  font-size: 1.25rem;
}
main.product-page main section.basic-info section.details div.size-select ul li.active {
  transform: scale(1.2);
  background: royalblue;
  color: #f0f0f0;
}
main.product-page main section.basic-info section.details div.stars {
  display: flex;
  margin-top: 0.5em;
}
main.product-page main section.basic-info section.details div.stars span {
  height: 25px;
  aspect-ratio: 1;
  display: block;
}
main.product-page main section.basic-info section.details div.stars span.active {
  color: gold;
}
main.product-page main section.basic-info section.details div.stars span svg {
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  main.product-page main section.product-suggestion,
main.product-page main section.cont-ingredients,
main.product-page main section.cont-info {
    width: 95%;
    margin: 0 auto;
  }
  main.product-page main section.basic-info {
    flex-direction: column;
  }
  main.product-page main section.basic-info section.image {
    height: auto;
    width: 100%;
  }
  main.product-page main section.basic-info section.details {
    width: 100%;
    padding: 0 0.5em;
  }
}