section.product-details {
  margin-bottom: 5em;
}
section.product-details h1 {
  text-align: center;
  padding-bottom: 1em;
  font-size: 1.75rem;
  font-weight: 700;
  font-family: "KoHo", sans-serif;
  letter-spacing: 1px;
}
section.product-details section.cont-ingredients h2,
section.product-details section.cont-info h2 {
  font-weight: 700;
  width: max-content;
  border-bottom: 2px solid #f0f0f0;
  margin-bottom: 1.25em;
  font-size: 1rem;
}
section.product-details section.cont-info {
  padding-top: 4em;
}
section.product-details section.cont-info div {
  display: flex;
  align-items: center;
  margin-bottom: 1.5em;
}
section.product-details section.cont-info div p {
  font-weight: 600;
  font-size: clamp(1.1rem, 5vw, 1.5rem);
}
section.product-details section.cont-info div span {
  margin-right: 0.5em;
  height: clamp(22px, 8vw, 35px);
  aspect-ratio: 1;
  display: block;
}
section.product-details section.cont-info div span svg {
  width: 100%;
  height: 100%;
}
section.product-details section.cont-ingredients p {
  font-family: "KoHo", sans-serif;
  font-weight: 500;
  line-height: 1.75em;
  font-size: clamp(1rem, 4vw, 1.15rem);
  margin-bottom: 2em;
}
section.product-details section.cont-ingredients p span {
  font-weight: 700;
  font-size: 1.5rem;
}