aside.basket {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 500px;
  max-width: 100%;
  background: #131313;
  transform: translateX(105%);
  transition: 0.5s;
  padding: 1em;
  border: 1px solid #000054;
}
aside.basket section.content {
  height: 96%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
aside.basket section.content h1.total {
  font-size: clamp(1.2rem, 4.5vw, 1.5rem);
  margin-top: 0.5em;
}
aside.basket section.content h1.price {
  text-align: right;
  text-transform: uppercase;
}
aside.basket section.content h1.price, aside.basket section.content h1.price span {
  font-size: clamp(1.1rem, 4.5vw, 1.4rem);
}
aside.basket section.content h1.price span {
  font-weight: 700;
  margin-left: 0.4em;
}
aside.basket section.content button {
  padding: 1em 0;
  background: rgb(36, 72, 180);
  outline: 0;
  border: 0;
  border-radius: 0.2em;
  cursor: pointer;
}
aside.basket section.content button, aside.basket section.content button > * {
  transition: 0.3s;
}
aside.basket section.content button label {
  font-weight: 500;
  font-family: "Roboto", sans-serif;
  font-size: clamp(1rem, 4.5vw, 1.5rem);
  text-transform: uppercase;
  color: #f0f0f0;
  letter-spacing: 1px;
  display: block;
}
aside.basket section.content button:hover {
  background: rgb(33, 61, 148);
}
aside.basket section.content button:hover label {
  letter-spacing: 0;
  transform: scale(0.9);
}
aside.basket section.content section.products-container {
  height: 70%;
}
aside.basket section.content section.products-container article.basket-item {
  width: 100%;
  height: 16%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5em 0.25em;
  background: #252525;
  border-radius: 0.15em;
}
aside.basket section.content section.products-container article.basket-item:not(:last-child) {
  margin-bottom: 2em;
}
aside.basket section.content section.products-container article.basket-item div.text-info {
  width: 50%;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
aside.basket section.content section.products-container article.basket-item div.text-info h4 {
  font-weight: 600;
  font-size: clamp(0.85rem, 4vw, 1rem);
  margin-bottom: 0.5em;
}
aside.basket section.content section.products-container article.basket-item div.text-info h5 {
  color: #909090;
  font-family: "KoHo", sans-serif;
  font-weight: 700;
  font-size: clamp(0.8rem, 4vw, 0.95rem);
}
aside.basket section.content section.products-container article.basket-item div.quantity-info,
aside.basket section.content section.products-container article.basket-item div.icon {
  display: flex;
  align-items: center;
  justify-content: center;
}
aside.basket section.content section.products-container article.basket-item div.quantity-info {
  width: 15%;
  border-radius: 0.5em;
  background: #151515;
  border: 2px solid #909090;
  font-size: clamp(0.9rem, 4vw, 1.2rem);
  font-weight: 500;
  letter-spacing: 1px;
  aspect-ratio: 1;
}
aside.basket section.content section.products-container article.basket-item div.icon {
  width: 10%;
}
aside.basket section.content section.products-container article.basket-item div.icon span {
  height: clamp(16px, 5vw, 20px);
  aspect-ratio: 1;
  display: block;
  cursor: pointer;
  transition: 0.3s;
  color: rgb(228, 17, 17);
}
aside.basket section.content section.products-container article.basket-item div.icon span svg {
  width: 100%;
  height: 100%;
  pointer-events: none;
}
aside.basket section.content section.products-container article.basket-item div.icon span:hover {
  color: rgb(255, 25, 71);
}
aside.basket section.content section.products-container article.basket-item figure {
  width: 15%;
  aspect-ratio: 1;
  overflow: hidden;
  z-index: 99;
}
aside.basket section.content section.products-container article.basket-item figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
aside.basket div.empty-cart {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  width: 100%;
}
aside.basket div.empty-cart svg {
  width: 30px;
  height: 30px;
  margin: 0 0.5em;
}
aside.basket div.empty-cart h2 {
  font-size: clamp(1.5rem, 4vw, 2rem);
  width: 100%;
  font-family: "KoHo", sans-serif;
  margin: 0.5em 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
aside.basket span.one-icon.cart {
  height: 4%;
  aspect-ratio: 1;
  display: block;
  margin: 0 0 0 auto;
  display: block;
  cursor: pointer;
}
aside.basket span.one-icon.cart svg {
  width: 100%;
  height: 100%;
}
@media (max-width: 480px) {
  aside.basket {
    padding: 0.5em 0;
    border: 0;
  }
  aside.basket section.content h1.total {
    padding-left: 0.5em;
  }
  aside.basket section.content h1.price {
    padding-right: 0.5em;
  }
  aside.basket section.content button {
    width: 90%;
    margin: 0 auto;
  }
  aside.basket span.one-icon.cart {
    margin: 0 0.75em 0em auto;
  }
}