header.homepage-header {
  height: calc(100vh - 64px);
  position: relative;
  z-index: 3;
}
header.homepage-header::before, header.homepage-header::after {
  position: absolute;
  content: "";
  width: clamp(40px, 25vw, 120px);
  height: clamp(30px, 10vw, 50px);
  background: rgba(65, 105, 225, 0.95);
  bottom: 0;
  transform: translateY(50%);
}
header.homepage-header::after {
  left: 0;
  border-radius: 0 0.5em 0.5em 0;
}
header.homepage-header::before {
  right: 0;
  border-radius: 0.5em 0 0 0.5em;
}
header.homepage-header main {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
header.homepage-header main h1 {
  text-align: center;
  padding: 0 0.25em;
  font-weight: 500;
  text-shadow: 0 0 3px black, 0 0 6px black;
  font-family: "KoHo", sans-serif;
}
header.homepage-header main h1, header.homepage-header main h1 span {
  font-size: calc(0.8rem + 10vmin);
}
header.homepage-header main h1 span {
  color: orange;
  font-style: italic;
  font-weight: 700;
}
header.homepage-header main p {
  margin: 3em 0;
  font-size: 1.4rem;
  font-size: clamp(0.8rem, 3vw, 1.4rem);
  padding: 0 0.5em;
  text-shadow: 0 0 3px black, 0 0 6px black;
  width: 850px;
  max-width: 95%;
  text-align: center;
  font-weight: 400;
}
header.homepage-header main div {
  display: flex;
}
header.homepage-header main div * {
  transition: 0.8s;
}
header.homepage-header main div button {
  padding: clamp(0.8em, 4vw, 1.2em) 0;
  width: clamp(120px, 30vw, 170px);
  max-width: 45vw;
  margin: 0 0.5em;
  border: 0;
  outline: 0;
  background: rgb(144, 1, 144);
  border-radius: 0.15em;
  box-shadow: 0 0 3px black;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
header.homepage-header main div button span {
  position: absolute;
  left: 50%;
  top: 100%;
  height: 30px;
  aspect-ratio: 1;
  display: block;
  transform: translate(-50%, 0);
}
header.homepage-header main div button span svg {
  width: 100%;
  height: 100%;
  color: #f0f0f0;
}
header.homepage-header main div button label {
  font-family: "KoHo", sans-serif;
  color: #f0f0f0;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.1rem;
  font-size: calc(0.55rem + 1vmin);
  display: block;
  letter-spacing: 1px;
}
header.homepage-header main div button:hover {
  background: rgb(57, 96, 212);
}
header.homepage-header main div button:hover label {
  transform: translateY(-200%);
}
header.homepage-header main div button:hover span {
  top: 50%;
  transform: translate(-50%, -50%);
}