section.tiles-section section.tile-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
section.tiles-section section.tile-container div.row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;
}
section.tiles-section section.tile-container article.tile {
  width: clamp(240px, 30vw, 300px);
  padding: 1.5em;
  background-color: #eb4511;
  background-image: linear-gradient(315deg, #eb4511 0%, #b02e0c 74%);
  aspect-ratio: 1.25;
  display: flex;
  justify-content: space-between;
  margin: 0.5em;
  flex-direction: column;
}
section.tiles-section section.tile-container article.tile button {
  background: none;
  border: 0;
  outline: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  width: max-content;
}
section.tiles-section section.tile-container article.tile button, section.tiles-section section.tile-container article.tile button > * {
  transition: 0.3s;
}
section.tiles-section section.tile-container article.tile button span, section.tiles-section section.tile-container article.tile button label {
  color: #f0f0f0;
}
section.tiles-section section.tile-container article.tile button span {
  margin-left: 0.5em;
  height: 25px;
  aspect-ratio: 1;
  display: block;
}
section.tiles-section section.tile-container article.tile button span svg {
  width: 100%;
  height: 100%;
}
section.tiles-section section.tile-container article.tile button label {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
}
section.tiles-section section.tile-container article.tile button:hover span, section.tiles-section section.tile-container article.tile button:hover label {
  color: rgb(116, 151, 255);
}
section.tiles-section section.tile-container article.tile button:hover label {
  text-shadow: 0 0 1px black;
}
section.tiles-section section.tile-container article.tile button:hover span {
  transform: scale(1.2) translateX(50%);
}
section.tiles-section section.tile-container article.tile div.stars {
  margin: 1.25em 0 2em 0;
  display: flex;
}
section.tiles-section section.tile-container article.tile div.stars span {
  height: 20px;
  aspect-ratio: 1;
  display: block;
}
section.tiles-section section.tile-container article.tile div.stars span.active {
  color: rgb(255, 184, 6);
}
section.tiles-section section.tile-container article.tile div.stars span svg {
  width: 100%;
  height: 100%;
}
section.tiles-section section.tile-container article.tile p {
  font-size: clamp(0.8rem, 3vw, 0.9rem);
  font-family: "KoHo", sans-serif;
}
@media (max-width: 480px) {
  section.tiles-section section.tile-container article.tile p {
    text-align: justify;
  }
}
section.tiles-section section.tile-container article.tile div.category {
  display: flex;
  align-items: center;
  margin-bottom: 0.25em;
}
section.tiles-section section.tile-container article.tile div.category h1 {
  font-weight: 900;
  font-family: "Lato", sans-serif;
  font-size: clamp(1.2rem, 4vw, 1.75rem);
  text-transform: uppercase;
}
section.tiles-section section.tile-container article.tile div.category span {
  height: clamp(20px, 5vw, 25px);
  aspect-ratio: 1;
  display: block;
  margin-left: 0.5em;
}
section.tiles-section section.tile-container article.tile div.category span svg {
  width: 100%;
  height: 100%;
}
section.tiles-section h1.entry-header {
  text-align: center;
  font-size: clamp(1.3rem, 5vw, 2.7rem);
  padding: 0.5em 0 1em 0;
  font-weight: 700;
  text-shadow: 0 0 1px #101010, 0 0 2px #101010, 0 0 3px #101010;
  word-spacing: 5px;
}
section.tiles-section h1.entry-header span {
  font-weight: 700;
  font-size: clamp(2.1rem, 5vw, 3.7rem);
  font-style: italic;
  color: orangered;
}
@media (max-width: 768px) {
  section.tiles-section h1.entry-header {
    padding: 1.5em 0;
  }
}