main.search-page nav {
  background: rgb(206, 71, 21);
}
main.search-page div.background-image.search-background div.search-background {
  backdrop-filter: blur(5px);
  padding-bottom: 4em;
}
main.search-page div.background-image.search-background div.search-background main {
  min-height: calc(100vh - 64px);
  margin: 0 auto;
  width: 80%;
}
main.search-page div.background-image.search-background div.search-background main div.search-bar {
  width: 500px;
  max-width: 95%;
  margin: 0 auto 1em auto;
  display: flex;
}
main.search-page div.background-image.search-background div.search-background main div.search-bar input {
  width: 90%;
  border-radius: 0.1em 0 0 0.1em;
  outline: 0;
  padding: 0 1em;
  color: #f0f0f0;
  font-family: "KoHo", sans-serif;
  font-size: clamp(0.9rem, 4vw, 1.2rem);
  background: rgba(34, 34, 34, 0.9);
  border: 1px solid orangered;
}
main.search-page div.background-image.search-background div.search-background main div.search-bar span {
  width: 10%;
  aspect-ratio: 1;
  background: orangered;
  display: flex;
  align-items: center;
  justify-content: center;
}
main.search-page div.background-image.search-background div.search-background main div.search-bar span svg {
  width: 50%;
  height: 50%;
}
main.search-page div.background-image.search-background div.search-background main figure.empty-search {
  width: 400px;
  max-width: 90%;
  margin: 0 auto;
  overflow: hidden;
}
main.search-page div.background-image.search-background div.search-background main figure.empty-search img {
  width: 100%;
}
main.search-page div.background-image.search-background div.search-background main section.result-container {
  min-height: 60vh;
  position: relative;
}
main.search-page div.background-image.search-background div.search-background main section.result-container div.loadingcss {
  border-radius: 0.2em;
  overflow: hidden;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container {
  border: 1px solid rgba(255, 68, 0, 0.9);
  border-radius: 0.2em;
  padding: 1em;
  height: inherit;
  background: rgba(30, 30, 30, 0.95);
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product {
  display: flex;
  height: 250px;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product:not(:last-child) {
  margin-bottom: 8em;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product figure {
  width: 32%;
  min-width: 200px;
  height: 100%;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section {
  width: 67%;
  padding: 0 1em 0.5em 1em;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section h4 {
  font-size: clamp(1.2rem, 4.5vw, 1.75rem);
  font-weight: 300;
  border-bottom: 1px solid #f0f0f0;
  width: max-content;
  color: rgb(231, 228, 228);
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap {
  display: flex;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button {
  margin-right: 0.75em;
  width: clamp(90px, 23vw, 125px);
  padding: 0.7em 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  outline: 0;
  border: 0;
  background: none;
  border-radius: 0.15em;
  color: #f0f0f0;
  cursor: pointer;
  transition: 0.5s;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button.add-to-cart {
  border: 1px solid royalblue;
  color: rgb(79, 121, 247);
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button.add-to-cart:hover {
  background: royalblue;
  color: #f0f0f0;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button.redirect {
  border: 1px solid rgb(202, 2, 202);
  color: rgb(215, 44, 215);
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button.redirect:hover {
  background: rgb(202, 2, 202);
  color: #f0f0f0;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button label {
  font-family: "KoHo", sans-serif;
  font-weight: 600;
  font-size: clamp(0.85rem, 4vw, 1rem);
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button span {
  height: 15px;
  aspect-ratio: 1;
  display: block;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button span svg {
  width: 100%;
  height: 100%;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.wrap h1 {
  margin-bottom: 0.5em;
  font-size: clamp(1.25rem, 5vw, 2.25rem);
  font-weight: 200;
  font-family: "KoHo", sans-serif;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.wrap div.stars {
  display: flex;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.wrap div.stars span {
  height: clamp(15px, 4.5vw, 22px);
  aspect-ratio: 1;
  display: block;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.wrap div.stars span.active {
  color: gold;
}
main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.wrap div.stars span svg {
  width: 100%;
  height: 100%;
}
main.search-page div.background-image.search-background div.search-background main h1.title {
  padding: 2em 0;
  font-family: "KoHo", sans-serif;
}
main.search-page div.background-image.search-background div.search-background main h1.title,
main.search-page div.background-image.search-background div.search-background main h1.title span {
  font-size: clamp(1.2rem, 5vw, 1.75rem);
}
main.search-page div.background-image.search-background div.search-background main h1.title span {
  font-weight: 700;
  margin-left: 0.5em;
}
@media (max-width: 768px) {
  main.search-page div.background-image.search-background div.search-background {
    backdrop-filter: blur(2px);
  }
  main.search-page div.background-image.search-background div.search-background main {
    width: 99%;
  }
  main.search-page div.background-image.search-background div.search-background main h1.title {
    margin-left: 0.75em;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container {
    padding: 0.25em;
  }
}
@media (max-width: 480px) {
  main.search-page div.background-image.search-background div.search-background {
    backdrop-filter: blur(2px);
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container {
    background: rgba(30, 30, 30, 0.8);
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product {
    height: auto;
    flex-direction: column;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product figure {
    width: 100%;
    height: auto;
    aspect-ratio: 2;
    margin-bottom: 0.5em;
    overflow: hidden;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section {
    width: 100%;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section h4 {
    margin: 1em 0;
    border: 0;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap {
    justify-content: space-between;
  }
  main.search-page div.background-image.search-background div.search-background main section.prod-container article.searched-product section div.btn-wrap button {
    width: 49%;
    margin: 0;
  }
}