section.product-suggestion {
  padding-bottom: 5em;
}
section.product-suggestion h1 {
  font-size: clamp(1.75rem, 5vw, 2rem);
  font-family: "KoHo", sans-serif;
  font-weight: 700;
  margin-bottom: 0.5em;
}
section.product-suggestion section {
  display: flex;
  padding-bottom: 0.5em;
}
@media (max-width: 768px) {
  section.product-suggestion section {
    overflow-x: scroll;
  }
  section.product-suggestion section::-webkit-scrollbar {
    height: 8px;
    background: #303030;
  }
  section.product-suggestion section::-webkit-scrollbar-thumb {
    height: 4px;
    background: royalblue;
    border-radius: 0.15em;
  }
}
section.product-suggestion article {
  position: relative;
  width: 17%;
  min-width: 130px;
  aspect-ratio: 1;
  cursor: pointer;
}
section.product-suggestion article:not(:last-child) {
  margin-right: 1em;
}
section.product-suggestion article div {
  z-index: 4;
  padding: 0.5em;
  position: relative;
  height: 100%;
}
section.product-suggestion article div h3 {
  font-family: "KoHo", sans-serif;
  font-weight: 600;
  font-size: 1.3rem;
  text-shadow: 0 0 4px #151515;
}
section.product-suggestion article div h4 {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: clamp(1rem, 2vw, 1.5rem);
  padding: 0.25em 0.75em;
  font-weight: 700;
  background: royalblue;
  border-radius: 0.15em 0 0 0.15em;
}
section.product-suggestion article figure {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
section.product-suggestion article figure img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 2s;
}
section.product-suggestion article:hover figure img {
  transform: scale(1.5);
}