main.terms-and-conditions nav {
  background: rgb(206, 71, 21);
}
main.terms-and-conditions main {
  background: #272727;
  color: #f0f0f0;
  padding: 5em clamp(0.5em, 8vw, 10em);
}
main.terms-and-conditions main h1 {
  font-weight: 900;
  font-family: "Lato", sans-serif;
  margin-bottom: 0.5em;
  font-size: clamp(2rem, 5vw, 2.5rem);
  text-transform: uppercase;
}
main.terms-and-conditions main h2 {
  margin-top: 1em;
  margin-bottom: 0.25em;
  font-weight: 600;
  font-size: clamp(1.5rem, 4vw, 2rem);
}
main.terms-and-conditions main p, main.terms-and-conditions main li {
  font-size: clamp(1rem, 4vw, 1.2rem);
  font-family: "KoHo", sans-serif;
  font-weight: 500;
}
main.terms-and-conditions main p {
  margin-bottom: 4em;
  text-align: justify;
  line-height: 1.5em;
}
main.terms-and-conditions main ul li {
  margin-bottom: 1em;
}
main.terms-and-conditions main ul li:last-child {
  margin-bottom: 2.5em;
}