main.finalize-order {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
main.finalize-order main.content {
  width: 1050px;
  max-width: 99%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
main.finalize-order main.content h1.summary,
main.finalize-order main.content h1.products {
  margin-bottom: 0.75em;
  color: #151515;
  font-weight: 600;
  font-size: clamp(1.2rem, 5vw, 1.75rem);
  text-transform: uppercase;
}
main.finalize-order main.content section.products {
  width: 55%;
  height: 100%;
}
main.finalize-order main.content section.products article.basket-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5em;
  height: clamp(75px, 25vw, 100px);
  width: 100%;
  background: whitesmoke;
  padding: 0.5em;
  color: #151515;
  border-radius: 0.15em;
  font-family: "KoHo", sans-serif;
}
main.finalize-order main.content section.products article.basket-item div.text-info {
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
main.finalize-order main.content section.products article.basket-item div.text-info h4 {
  font-weight: 700;
  text-transform: uppercase;
  font-size: clamp(0.8rem, 3.5vw, 1rem);
}
main.finalize-order main.content section.products article.basket-item div.text-info h5 {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
}
main.finalize-order main.content section.products article.basket-item div.quantity-info {
  width: 10%;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(235, 235, 235);
  border-radius: 0.15em;
  border: 1px solid rgb(225, 225, 225);
  font-weight: 600;
  font-size: clamp(1rem, 5vw, 1.25rem);
}
main.finalize-order main.content section.products article.basket-item figure {
  margin-right: 0.5em;
  aspect-ratio: 1;
  height: 100%;
  overflow: hidden;
  border-radius: 0.3em;
}
main.finalize-order main.content section.products article.basket-item figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
main.finalize-order main.content form.buy-method-cont {
  width: 40%;
  min-width: 350px;
  height: 100%;
  border-radius: 0.2em;
  padding: 0.5em;
  position: relative;
  color: #151515;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
main.finalize-order main.content form.buy-method-cont h6.result {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0.75em 0;
  text-align: center;
  font-size: 1.1rem;
  color: #f0f0f0;
  font-weight: 700;
  font-family: "KoHo", sans-serif;
}
main.finalize-order main.content form.buy-method-cont h6.result.true {
  background: green;
}
main.finalize-order main.content form.buy-method-cont h6.result.false {
  background: red;
}
main.finalize-order main.content form.buy-method-cont section.delivery-method,
main.finalize-order main.content form.buy-method-cont section.payment-method {
  padding: 0.75em;
  background: rgb(218, 224, 242);
}
main.finalize-order main.content form.buy-method-cont section.delivery-method section.delivery,
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: whitesmoke;
  padding: 1em 1.5em;
}
main.finalize-order main.content form.buy-method-cont section.delivery-method section.delivery:not(:last-child),
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment:not(:last-child) {
  margin-bottom: 0.5em;
}
main.finalize-order main.content form.buy-method-cont section.delivery-method section.delivery input,
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment input {
  cursor: pointer;
}
main.finalize-order main.content form.buy-method-cont section.delivery-method section.delivery label,
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment label {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: clamp(0.8rem, 4vw, 1rem);
}
main.finalize-order main.content form.buy-method-cont section.delivery-method section.delivery span {
  font-weight: 600;
  font-family: "KoHo", sans-serif;
}
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment span {
  height: 30px;
  aspect-ratio: 1;
  display: block;
}
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment span svg {
  width: 100%;
  height: 100%;
}
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment.mastercard span {
  color: orangered;
}
main.finalize-order main.content form.buy-method-cont section.payment-method section.payment.paypal span {
  color: royalblue;
}
main.finalize-order main.content form.buy-method-cont section.summary-price {
  padding: 1.5em;
  border-radius: 0.25em;
  background: rgb(212, 220, 240);
  width: 75%;
  margin: 0 auto;
  font-family: "KoHo", sans-serif;
}
main.finalize-order main.content form.buy-method-cont section.summary-price h4 {
  font-size: 1.2rem;
  font-weight: 600;
}
main.finalize-order main.content form.buy-method-cont section.summary-price h4.middle {
  margin: 1em 0;
}
main.finalize-order main.content form.buy-method-cont section.summary-price h2 {
  font-size: 2rem;
  font-weight: 700;
}
main.finalize-order main.content form.buy-method-cont button.order-pay {
  width: 100%;
  background: royalblue;
  padding: 0.75em 0;
  outline: 0;
  border: 0;
  cursor: pointer;
  transition: 0.3s;
}
main.finalize-order main.content form.buy-method-cont button.order-pay label {
  font-size: 1.1rem;
  text-transform: uppercase;
  color: #f0f0f0;
  font-weight: 700;
  font-family: "KoHo", sans-serif;
}
main.finalize-order main.content form.buy-method-cont button.order-pay:hover {
  background: rebeccapurple;
}
main.finalize-order main.content form.buy-method-cont span.home {
  position: absolute;
  right: 0;
  top: 0;
  height: 25px;
  aspect-ratio: 1;
  display: block;
  cursor: pointer;
}
main.finalize-order main.content form.buy-method-cont span.home svg {
  width: 100%;
  height: 100%;
}
@media (max-width: 480px) {
  main.finalize-order main.content form.buy-method-cont span.home {
    position: fixed;
    right: 8px;
    top: 8px;
  }
}
@media (max-width: 768px) {
  main.finalize-order main.content {
    flex-direction: column;
  }
  main.finalize-order main.content section.products,
main.finalize-order main.content form.buy-method-cont {
    width: 98%;
    min-width: auto;
  }
  main.finalize-order main.content form.buy-method-cont {
    margin-top: 2em;
  }
  main.finalize-order main.content form.buy-method-cont section.summary-price {
    width: 100%;
    margin: 1em 0;
  }
  main.finalize-order main.content form.buy-method-cont section.delivery-method {
    margin-top: 1.5em;
  }
  main.finalize-order main.content form.buy-method-cont section.delivery-method,
main.finalize-order main.content form.buy-method-cont section.payment-method {
    padding: 0;
  }
}