section.new-product-container {
  width: 90%;
  margin: 0 auto;
  padding: 4em 0;
  overflow: hidden;
}
section.new-product-container section.container {
  margin-top: 3em;
  display: flex;
  align-items: center;
  justify-content: space-around;
  align-items: stretch;
  flex-wrap: wrap;
}
section.new-product-container section.container article.product {
  width: clamp(280px, 28vw, 350px);
  background: rgba(244, 244, 244, 0.83);
  border-radius: 0.15em;
  color: #101010;
  margin: 0 0.5em 3em 0.5em;
}
section.new-product-container section.container article.product figure {
  width: 100%;
  height: 50%;
  overflow: hidden;
}
section.new-product-container section.container article.product figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
section.new-product-container section.container article.product article {
  padding: 0.5em;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
section.new-product-container section.container article.product article div.btn-wrap {
  display: flex;
  justify-content: space-between;
}
section.new-product-container section.container article.product article div.btn-wrap button {
  width: 100%;
  padding: clamp(0.3em, 2.8vw, 0.75em) 0;
  outline: 0;
  border: 0;
  border-radius: 0.15em;
  cursor: pointer;
  position: relative;
  color: #f0f0f0;
  overflow: hidden;
}
section.new-product-container section.container article.product article div.btn-wrap button.redirect {
  width: 30%;
  background: purple;
}
section.new-product-container section.container article.product article div.btn-wrap button.add-to-cart {
  width: 55%;
  background: royalblue;
}
section.new-product-container section.container article.product article div.btn-wrap button, section.new-product-container section.container article.product article div.btn-wrap button * {
  transition: 0.5s;
}
section.new-product-container section.container article.product article div.btn-wrap button span {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 105%;
  pointer-events: none;
  height: 25px;
  aspect-ratio: 1;
  display: block;
}
section.new-product-container section.container article.product article div.btn-wrap button span svg {
  width: 100%;
  height: 100%;
}
section.new-product-container section.container article.product article div.btn-wrap button label {
  font-weight: 700;
  font-family: "KoHo", sans-serif;
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  text-transform: uppercase;
  display: block;
}
section.new-product-container section.container article.product article div.btn-wrap button:hover.redirect {
  background: #4d004d;
}
section.new-product-container section.container article.product article div.btn-wrap button:hover.add-to-cart {
  background: #1a3ca2;
}
section.new-product-container section.container article.product article div.btn-wrap button:hover label {
  transform: translateY(-200%);
}
section.new-product-container section.container article.product article div.btn-wrap button:hover span {
  top: 50%;
  transform: translate(-50%, -50%);
}
section.new-product-container section.container article.product article div.wrap {
  display: flex;
  justify-content: space-between;
}
section.new-product-container section.container article.product article div.wrap h3 {
  font-size: clamp(1rem, 3vw, 1.5rem);
  width: 75%;
  font-weight: 900;
  font-family: "Lato", sans-serif;
}
section.new-product-container section.container article.product article div.wrap h4 {
  font-weight: 600;
  font-size: clamp(1.3rem, 4vw, 1.5rem);
}
section.new-product-container section.container article.product article p {
  font-family: "KoHo", sans-serif;
  font-weight: 500;
  margin-bottom: 0.5em;
  font-size: clamp(0.85rem, 3vw, 1rem);
}
@media (max-width: 768px) {
  section.new-product-container {
    width: 98%;
  }
}
@media (max-width: 480px) {
  section.new-product-container section.container {
    align-items: flex-start;
  }
  section.new-product-container section.container article.product figure {
    height: 130px;
  }
  section.new-product-container section.container article.product article p {
    display: none;
  }
  section.new-product-container section.container article.product article div.wrap {
    margin-bottom: 1em;
  }
}