nav.layout-navigation {
  height: 64px;
  padding: 0.5em 2em;
  display: flex;
  align-items: center;
  z-index: 9999;
  position: relative;
  width: 100%;
  transition: 0.05s;
}
nav.layout-navigation div.menu-icon {
  height: clamp(20px, 5vw, 25px);
  aspect-ratio: 1;
  margin-left: 1.5em;
  position: relative;
  cursor: pointer;
}
nav.layout-navigation div.menu-icon span {
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  background: #f0f0f0;
  pointer-events: none;
}
nav.layout-navigation div.icons {
  margin: 0 0 0 auto;
  display: flex;
  align-items: center;
}
nav.layout-navigation div.icons div.menu-icon span:nth-child(1) {
  top: 10%;
}
nav.layout-navigation div.icons div.menu-icon span:nth-child(2) {
  top: 50%;
}
nav.layout-navigation div.icons div.menu-icon span:nth-child(3) {
  top: 85%;
}
nav.layout-navigation div.icons span.one-icon {
  height: clamp(20px, 5vw, 25px);
  aspect-ratio: 1;
  display: block;
  margin-left: 1em;
  cursor: pointer;
  transition: 0.3s;
}
nav.layout-navigation div.icons span.one-icon.search {
  position: relative;
}
nav.layout-navigation div.icons span.one-icon.search input {
  position: absolute;
  top: -300%;
  opacity: 0;
  right: 0;
  width: 250px;
  max-width: 99vw;
  font-size: 1rem;
  outline: 0;
  padding: 0.75em 0.5em;
  border: 1px solid orange;
  border-radius: 0.15em;
  background: rgb(204, 54, 0);
  color: #f0f0f0;
  font-family: "KoHo", sans-serif;
  font-weight: 700;
  transition: 0.2s;
}
nav.layout-navigation div.icons span.one-icon.search input::placeholder {
  font-size: 0.8rem;
  font-family: "KoHo", sans-serif;
  color: #f0f0f0;
  opacity: 0.9;
}
nav.layout-navigation div.icons span.one-icon svg {
  width: 100%;
  height: 100%;
  pointer-events: none;
}
nav.layout-navigation div.icons span.one-icon.cart:hover {
  color: rgb(77, 118, 241);
}
nav.layout-navigation ul.upper-links {
  display: flex;
  list-style: none;
}
nav.layout-navigation ul.upper-links li {
  margin-right: 2em;
}
nav.layout-navigation ul.upper-links li a {
  font-weight: 400;
  font-size: 1.15rem;
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;
  color: #f0f0f0;
}
nav.layout-navigation ul.upper-links li a:hover {
  color: rgb(178, 197, 255);
}
nav.layout-navigation ul.upper-links li.menu {
  position: relative;
}
nav.layout-navigation ul.upper-links li.menu::before {
  width: 100%;
  content: "";
  position: absolute;
  bottom: -5px;
  transition: 0.3s;
  left: 0;
  height: 1.5px;
  background: #f0f0f0;
}
nav.layout-navigation ul.upper-links li.menu:hover::before {
  bottom: -10px;
  background: rgb(178, 197, 255);
}
nav.layout-navigation figure.logo {
  height: 100%;
  overflow: hidden;
  padding: 0 3em;
}
nav.layout-navigation figure.logo img {
  height: 100%;
}
@media (max-width: 768px) {
  nav.layout-navigation figure.logo {
    display: none;
  }
}
@media (max-width: 480px) {
  nav.layout-navigation ul.upper-links {
    display: none;
  }
  nav.layout-navigation figure.logo {
    display: block;
    padding: 0;
  }
}