section.popular-product-container {
  width: 95%;
  margin: 0 auto;
}
section.popular-product-container h2.popular-product-container {
  margin: 1em 0 2em 0;
}
section.popular-product-container section.container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: stretch;
}
section.popular-product-container section.container article.product {
  width: 45%;
  min-width: 600px;
  flex-wrap: wrap;
  margin: 0 0.25em 3em 0.25em;
  display: flex;
  background: rgba(244, 244, 244, 0.83);
  position: relative;
}
section.popular-product-container section.container article.product figure {
  width: 40%;
  overflow: hidden;
}
section.popular-product-container section.container article.product figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
section.popular-product-container section.container article.product article {
  width: 60%;
  padding: 0.5em 1em;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  color: #151515;
}
section.popular-product-container section.container article.product article ul {
  list-style: none;
  flex-wrap: wrap;
  display: flex;
  align-items: none;
  justify-content: center;
}
section.popular-product-container section.container article.product article ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "KoHo", sans-serif;
  font-size: 1.1rem;
  font-weight: 700;
  width: 50%;
  margin-bottom: 1em;
}
section.popular-product-container section.container article.product article ul li svg {
  width: 20px;
  height: 20px;
  margin-right: 0.5em;
}
section.popular-product-container section.container article.product article p {
  font-family: "KoHo", sans-serif;
  font-size: clamp(0.85rem, 3vw, 1rem);
  position: relative;
  text-align: center;
  font-weight: 600;
  border-top: 1px solid #151515;
  border-bottom: 1px solid #151515;
  padding: 0.5em 0;
  margin: 1em 0 2em 0;
}
section.popular-product-container section.container article.product article section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 1.5em 0 2em 0;
}
section.popular-product-container section.container article.product article section div {
  display: flex;
  align-items: center;
}
section.popular-product-container section.container article.product article section div h4 {
  color: goldenrod;
  font-size: 1.5rem;
  margin-right: 0.25em;
  font-weight: 500;
}
section.popular-product-container section.container article.product article section div span {
  height: clamp(20px, 5vw, 25px);
  aspect-ratio: 1;
  display: block;
  color: whitesmoke;
}
section.popular-product-container section.container article.product article section div span.active {
  color: goldenrod;
}
section.popular-product-container section.container article.product article section div span:not(.active) {
  filter: drop-shadow(0px 0px 1px #151515);
}
section.popular-product-container section.container article.product article section div span svg {
  width: 100%;
  height: 100%;
}
section.popular-product-container section.container article.product article section h3 {
  font-weight: 900;
  font-size: clamp(1.5rem, 4vw, 1.7rem);
}
section.popular-product-container section.container article.product article div.btn-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
section.popular-product-container section.container article.product article div.btn-wrap button {
  padding: clamp(0.4em, 2.5vw, 0.75em) 0;
  outline: 0;
  border: 0;
  cursor: pointer;
  color: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 0.15em;
}
section.popular-product-container section.container article.product article div.btn-wrap button span {
  height: 25px;
  aspect-ratio: 1;
  display: block;
  pointer-events: none;
}
section.popular-product-container section.container article.product article div.btn-wrap button span svg {
  width: 100%;
  height: 100%;
}
section.popular-product-container section.container article.product article div.btn-wrap button label {
  font-family: "KoHo", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
}
section.popular-product-container section.container article.product article div.btn-wrap button, section.popular-product-container section.container article.product article div.btn-wrap button > * {
  transition: 0.5s;
}
section.popular-product-container section.container article.product article div.btn-wrap button.add-to-cart {
  width: 50%;
  background: royalblue;
  min-width: 140px;
}
section.popular-product-container section.container article.product article div.btn-wrap button.redirect {
  width: 30%;
  min-width: 100px;
  background: purple;
}
section.popular-product-container section.container article.product article div.btn-wrap button:hover {
  filter: brightness(90%);
}
section.popular-product-container section.container article.product article div.btn-wrap button:hover span {
  transform: rotate(360deg);
}
section.popular-product-container section.container article.product article h1 {
  text-align: center;
  font-size: clamp(1.5rem, 5vw, 2rem);
  font-weight: 600;
  font-family: "KoHo", sans-serif;
}
@media (max-width: 480px) {
  section.popular-product-container section.container article.product figure {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    filter: blur(5px);
  }
  section.popular-product-container section.container article.product article {
    width: 100%;
    z-index: 3;
    color: #f0f0f0;
    text-shadow: 0 0 2px #151515;
  }
  section.popular-product-container section.container article.product article p {
    border-color: #f0f0f0;
  }
  section.popular-product-container section.container article.product article ul li svg {
    filter: drop-shadow(0 0 1px black);
  }
  section.popular-product-container section.container article.product article section div h4, section.popular-product-container section.container article.product article section div span.active {
    color: gold;
  }
}
@media (max-width: 768px) {
  section.popular-product-container {
    width: 99%;
  }
  section.popular-product-container section.container article.product {
    min-width: auto;
    width: 600px;
    max-width: 100%;
    margin: 0;
    margin-bottom: 3em;
  }
}