main.contact-page nav {
  background: rgb(206, 71, 21);
}
main.contact-page main {
  background: #272727;
}
main.contact-page main form {
  width: 45%;
  min-width: 450px;
  padding: 5em;
}
main.contact-page main form button {
  outline: 0;
  border: 2px solid rgb(177, 47, 0);
  background: rgb(234, 67, 6);
  padding: 0.75em 0;
  width: 120px;
  border-radius: 1em;
  cursor: pointer;
  transition: 0.3s;
}
main.contact-page main form button label {
  font-family: "KoHo", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1rem;
  color: #f0f0f0;
}
main.contact-page main form button:hover {
  background: #d13c05;
  box-shadow: 0 0 2px orangered, 0 0 4px orangered;
}
main.contact-page main form h1 {
  text-transform: uppercase;
  font-family: "KoHo", sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
  margin-bottom: 1em;
}
main.contact-page main form input,
main.contact-page main form textarea {
  padding: 1em;
  outline: 0;
  border: 0;
  background: #404040;
  border-radius: 0.1em;
  display: block;
  width: 100%;
  margin-bottom: 1em;
  color: #f0f0f0;
  font-size: 1rem;
  font-family: "KoHo", sans-serif;
}
main.contact-page main form input::placeholder,
main.contact-page main form textarea::placeholder {
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 700;
  color: #f0f0f0;
}
main.contact-page main form textarea {
  resize: none;
  height: 250px;
}
main.contact-page main section.informations {
  background: #353533;
  padding: 4em;
  position: relative;
}
main.contact-page main section.informations section.contact-details {
  position: absolute;
  border-radius: 0.2em;
  width: 35%;
  right: 0;
  top: 0;
  transform: translate(-25%, -50%);
  background: #404040;
  padding: 1.5em;
}
main.contact-page main section.informations section.contact-details div {
  font-family: "KoHo", sans-serif;
}
main.contact-page main section.informations section.contact-details div:not(:last-child) {
  margin-bottom: 2.5em;
}
main.contact-page main section.informations section.contact-details div h2 {
  text-transform: uppercase;
  font-weight: 700;
  font-size: clamp(1.2rem, 4vw, 1.5rem);
  width: max-content;
  border-bottom: 2px solid #f0f0f0;
  margin-bottom: 0.5em;
}
main.contact-page main section.informations section.contact-details div p {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: clamp(1rem, 4vw, 1.1rem);
  color: #d2d2d2;
  letter-spacing: 1px;
}
main.contact-page main section.informations section.contact-details div p span.clipboard {
  height: 25px;
  aspect-ratio: 1;
  display: block;
  margin-left: 1em;
  cursor: pointer;
  position: relative;
  transition: 0.2s;
}
main.contact-page main section.informations section.contact-details div p span.clipboard span {
  position: absolute;
  font-size: 0.9rem;
  pointer-events: none;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  color: #f0f0f0;
}
main.contact-page main section.informations section.contact-details div p span.clipboard svg {
  pointer-events: none;
  width: 100%;
  height: 100%;
}
main.contact-page main section.informations section.contact-details div:last-child p {
  cursor: pointer;
  text-transform: uppercase;
  position: relative;
  width: max-content;
}
main.contact-page main section.informations section.contact-details div:last-child p::before {
  content: "";
  left: 50%;
  position: absolute;
  top: 100%;
  transform: translateX(-50%);
  height: 1px;
  width: 0%;
  background: #d2d2d2;
  transition: 0.4s;
}
main.contact-page main section.informations section.contact-details div:last-child p:hover::before {
  width: 100%;
}
main.contact-page main section.informations section.wrap {
  width: 45%;
}
main.contact-page main section.informations section.wrap h1 {
  font-weight: 700;
  text-transform: uppercase;
  font-size: clamp(1.75rem, 5vw, 3rem);
}
main.contact-page main section.informations section.wrap p {
  margin: 1.5em 0;
  font-size: clamp(1rem, 4vw, 1.2rem);
  font-weight: 600;
  font-family: "KoHo", sans-serif;
}
main.contact-page main section.informations div.icons {
  display: flex;
}
main.contact-page main section.informations div.icons span {
  height: 35px;
  aspect-ratio: 1;
  display: block;
  margin-right: 0.75em;
}
main.contact-page main section.informations div.icons span svg {
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  main.contact-page main form {
    width: 100%;
  }
  main.contact-page main section.informations section.contact-details {
    position: static;
    transform: translate(0, 0);
    width: 100%;
    margin-top: 2em;
  }
  main.contact-page main section.informations section.wrap {
    width: 100%;
  }
}
@media (max-width: 480px) {
  main.contact-page main form,
main.contact-page main section.informations {
    padding: 4em 0.5em;
  }
  main.contact-page main form {
    min-width: auto;
  }
  main.contact-page main form textarea {
    height: 200px;
  }
}