section.opinions-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
section.opinions-container h1.title {
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 0 0 2px #151515;
}
section.opinions-container h1.title,
section.opinions-container h1.title span.customers {
  font-size: clamp(1.2rem, 4vw, 1.5rem);
  font-weight: 700;
}
section.opinions-container h1.title svg {
  width: 100%;
  height: 100%;
}
section.opinions-container h1.title span.customers {
  position: relative;
  margin: 0 0.3em;
}
section.opinions-container h1.title span.customers span.heart {
  height: 20px;
  aspect-ratio: 1;
  display: block;
  color: crimson;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 100%;
}
section.opinions-container h1.title span.customers span.heart svg {
  filter: drop-shadow(0 0 1px white);
}
section.opinions-container h1.title span.question-mark {
  height: 35px;
  aspect-ratio: 1;
  display: block;
}
section.opinions-container div.container {
  width: 75%;
  width: 1150px;
  max-width: 98%;
  height: 80%;
  position: relative;
  overflow: hidden;
}
section.opinions-container div.container section.slide-buttons {
  position: absolute;
  width: max-content;
  bottom: 2em;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: flex-end;
}
section.opinions-container div.container section.slide-buttons span {
  height: 15px;
  aspect-ratio: 1;
  display: block;
  border-radius: 50%;
  background: royalblue;
  margin: 0 0.5em;
  cursor: pointer;
  transition: 0.5s;
  border: 1px solid rgb(167, 198, 255);
}
section.opinions-container div.container section.slide-buttons span.active {
  pointer-events: none;
  transform: scale(1.5);
  background: rgb(163, 195, 255);
  border-color: rgb(71, 71, 160);
}
section.opinions-container div.container section.slide-buttons span:hover {
  transform: scale(1.2);
}
section.opinions-container div.container section.moving-element {
  height: 100%;
  display: flex;
  transition: 0.8s;
  transform: translateX(-100%);
}
section.opinions-container div.container section.moving-element article.opinion {
  height: 100%;
  width: 100%;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
section.opinions-container div.container section.moving-element article.opinion section.user {
  width: 32%;
  height: 100%;
  border-radius: 2em;
  background: rgba(65, 105, 225, 0.81);
  position: relative;
}
section.opinions-container div.container section.moving-element article.opinion section.user::before, section.opinions-container div.container section.moving-element article.opinion section.user::after {
  content: "";
  position: absolute;
  left: 100%;
  background: rgba(65, 105, 225, 0.81);
  width: 100%;
  height: 20px;
}
section.opinions-container div.container section.moving-element article.opinion section.user::before {
  top: 2em;
}
section.opinions-container div.container section.moving-element article.opinion section.user::after {
  bottom: 2em;
}
section.opinions-container div.container section.moving-element article.opinion section.user div {
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
section.opinions-container div.container section.moving-element article.opinion section.user div h2 {
  text-align: center;
  margin: 0.2em 0;
  font-size: clamp(1.2rem, 4vw, 1.75rem);
  font-family: "KoHo", sans-serif;
  font-weight: 700;
}
section.opinions-container div.container section.moving-element article.opinion section.user figure {
  height: 70%;
  padding: 1em;
}
section.opinions-container div.container section.moving-element article.opinion section.user figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2em;
}
section.opinions-container div.container section.moving-element article.opinion article.text {
  width: 68%;
  height: 80%;
  padding: 1em;
  border-radius: 0 0.2em 0.2em 0;
  background: rgba(65, 105, 225, 0.6);
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap {
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p {
  font-family: "KoHo", sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  letter-spacing: 1px;
  width: 80%;
  text-align: justify;
  position: relative;
  line-height: clamp(1.5em, 3vw, 2.5em);
  position: relative;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span {
  position: absolute;
  height: 30px;
  aspect-ratio: 1;
  display: block;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span svg {
  width: 100%;
  height: 100%;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:first-of-type {
  left: 0;
  top: 0;
  transform: translate(-150%, -50%);
}
section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:last-of-type {
  right: 0;
  bottom: 0;
  transform: translate(150%, 50%);
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap {
  display: flex;
  align-items: center;
  height: 10%;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap h1 {
  font-weight: 900;
  margin-left: 0.5em;
  font-size: clamp(1.3rem, 3.5vw, 2.5rem);
  font-family: "Lato", sans-serif;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap div.stars {
  display: flex;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap div.stars span {
  height: clamp(20px, 3vw, 30px);
  aspect-ratio: 1;
  display: block;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap div.stars span.active {
  color: gold;
}
section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap div.stars span svg {
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  section.opinions-container div.container section.moving-element article.opinion {
    flex-direction: column;
  }
  section.opinions-container div.container section.moving-element article.opinion section.user {
    height: clamp(10%, 15vw, 15%);
    width: 98%;
    border-radius: 0.2em 0.2em 0 0;
    display: flex;
    align-items: center;
  }
  section.opinions-container div.container section.moving-element article.opinion section.user div {
    flex-direction: row;
  }
  section.opinions-container div.container section.moving-element article.opinion section.user div h2 {
    margin-right: 0.5em;
  }
  section.opinions-container div.container section.moving-element article.opinion section.user figure {
    height: 100%;
    width: auto;
    aspect-ratio: 1;
  }
  section.opinions-container div.container section.moving-element article.opinion section.user::before, section.opinions-container div.container section.moving-element article.opinion section.user::after {
    display: none;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text {
    height: 85%;
    width: 98%;
    border-radius: 0 0 0.2em 0.2em;
  }
}
@media (max-width: 480px) {
  section.opinions-container {
    justify-content: center;
  }
  section.opinions-container div.container section.slide-buttons {
    position: static;
    width: 100%;
    justify-content: center;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.tr-wrap {
    height: auto;
    flex-direction: column;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p {
    line-height: 1.2em;
    width: 95%;
    text-align: center;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span {
    height: 40vw;
    aspect-ratio: 1;
    display: block;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:first-of-type, section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:last-of-type {
    opacity: 0.15;
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:first-of-type {
    left: 50%;
    top: 0;
    transform: translate(0, -50%);
  }
  section.opinions-container div.container section.moving-element article.opinion article.text div.text-wrap p span:last-of-type {
    right: 50%;
    bottom: 0;
    transform: translate(0, 50%);
  }
}