aside.menu {
  position: fixed;
  top: 0;
  right: 0;
  height: 600px;
  width: 350px;
  max-width: 100%;
  background: #000039;
  border-radius: 0 0 0 0.5em;
  transform: translateX(105%);
  transition: 0.5s;
  padding: 0.5em 1em;
  border: 1px solid #000054;
}
aside.menu ul {
  list-style: none;
}
aside.menu ul li a {
  color: #f0f0f0;
  text-decoration: none;
  font-weight: 600;
  padding: 1.5em 1em;
  font-family: "KoHo", sans-serif;
  font-size: clamp(0.9rem, 3vw, 1.1rem);
  transition: 0.4s;
  display: flex;
  align-items: center;
  border-radius: 0.2em;
}
aside.menu ul li a svg {
  width: 20px;
  height: 20px;
  margin-right: 1em;
}
aside.menu ul li a:hover {
  background: rgba(46, 39, 66, 0.5);
}
aside.menu div.icon-and-logo {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2em;
}
aside.menu div.icon-and-logo figure {
  height: 100%;
  overflow: hidden;
}
aside.menu div.icon-and-logo figure img {
  height: 100%;
}
aside.menu div.menu-icon {
  margin-right: 0.5em;
}
aside.menu div.menu-icon span {
  top: 50%;
}
aside.menu div.menu-icon span:nth-child(1), aside.menu div.menu-icon span:nth-child(2) {
  transform: rotate(45deg);
}
aside.menu div.menu-icon span:nth-child(3) {
  transform: rotate(-45deg);
}
@media (max-width: 480px) {
  aside.menu {
    height: 100vh;
  }
}