footer {
  background: rgb(105, 50, 11);
  background: rgb(206, 71, 21);
  padding: 4em 0;
}
footer section.lists,
footer section.finish {
  margin: 0 auto;
  width: 80%;
}
footer section.lists {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
footer section.lists ul {
  list-style: none;
}
footer section.lists ul li {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 900;
  font-size: clamp(0.7rem, 3vw, 1.2rem);
  margin-bottom: 0.4em;
  cursor: pointer;
  transition: 0.3s;
  font-family: "Lato", sans-serif;
}
footer section.lists ul li:hover {
  color: rgb(178, 197, 255);
}
footer section.lists figure {
  overflow: hidden;
  width: 10%;
}
footer section.lists figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
footer section.finish {
  border-top: 1px solid #f0f0f0;
  padding-top: 3em;
  margin-top: 3em;
}
footer section.finish p {
  text-align: center;
  text-transform: uppercase;
  font-size: clamp(0.8rem, 3vw, 0.9rem);
  font-weight: 700;
  margin-top: 2em;
}
footer section.finish div.icons {
  display: flex;
  align-items: center;
  justify-content: center;
}
footer section.finish div.icons span {
  height: clamp(30px, 8vw, 50px);
  aspect-ratio: 1;
  display: block;
  transition: 0.4s;
  margin: 0 0.5em;
  cursor: pointer;
}
footer section.finish div.icons span svg {
  width: 100%;
  height: 100%;
}
footer section.finish div.icons span:hover {
  transform: scale(1.2);
  filter: drop-shadow(0 0 3px royalblue);
}
@media (max-width: 768px) {
  footer section.lists figure {
    display: none;
  }
}
@media (max-width: 480px) {
  footer section.lists,
footer section.finish {
    width: 95%;
  }
}